.ReactTable.-striped.-highlight {
	$padding: 19px 12px;
	$padding-outside: 25px;

	font-size: 16px;
	border: none;

	// --- MODE: Full screen
	&.full {
		.rt-table {
			border-top-left-radius: 10px;
			border-top-right-radius: 10px;
		}

		.rt-thead {
			border-top-left-radius: 10px;
			border-top-right-radius: 10px;
		}
	}

	// HEADER: Align
	&.header-align-left {
		.rt-thead {
			.rt-tr {
				text-align: left;
			}
		}
	}

	// ---- MODE: Normal
	// HEADER
	.rt-thead {
		background: $table-header-bg;
		box-shadow: 0 1px 0px 0 $border-1;
		font-size: 18px;
		font-weight: 700;

		.rt-tr {
			.rt-th {
				padding: $padding;
				color: $text-color;
				border-right: none;

				&:first-child {
					padding-left: $padding-outside;
				}
				&:last-child {
					padding-right: $padding-outside;
				}
			}
		}
	}

	// BODY
	.rt-tbody {
		min-height: 200px;

		.rt-tr-group {
			border-bottom: solid 1px $border-1;

			.rt-tr {
				background: transparent;

				&:hover {
					background: $hover;
				}

				.rt-td {
					border-right: none;
					padding: $padding;

					&:first-child {
						padding-left: $padding-outside;
					}
					&:last-child {
						padding-right: $padding-outside;
					}
				}
			}
		}
	}

	.rt-noData {
		background: transparent;
		color: $text-color;
		padding: 40px 0 0px;
	}

	// PAGING CUSTOM
	.pagination_custom {
		display: flex;
		justify-content: flex-end;
		padding: 18px 26px;
		// border-top: 1px solid #e6e7e9;

		.pageButton {
			color: $text-color;
			padding: 8px 13px;
			outline: none;
			border: none;
			background-color: transparent;
			cursor: pointer;

			& + .pageButton {
				margin-left: 5px;
			}

			&:disabled {
				cursor: not-allowed;
				color: gray;
			}

			&--active {
				color: #096af5;
				font-weight: bold;
				border-radius: 4px;
				background: rgba(9, 106, 245, 0.2);
			}
		}

		.next,
		.pre {
			.pageButton {
				display: flex;
				align-items: center;
			}
		}

		span {
			margin: auto 10px;
		}
	}
}

// Mui: PAGING CUSTOM
.pagination_mui_custom {
	display: flex;
	justify-content: flex-end;
	padding: 18px 26px;
	font-size: 16px;
	font-weight: 700;

	.pageButton {
		font-size: 16px;
		font-weight: 700;
		color: $text-color;
		border: none;
		background: transparent;

		height: 38px;
		width: 37px;

		&--active {
			color: #096af5;
			background: rgba(9, 106, 245, 0.2);
		}

		&--ellipsis {
			justify-content: center;
			align-items: center;
			display: flex;
			margin: 0 10px;
		}
	}
}

.btn-copy {
	cursor: pointer;
	background: $icon-bg;
	padding: 4px 5px;
	border-radius: 4px;
	line-height: 0;

	img {
		height: 15px;
		width: 12px;
	}

	&:active {
		opacity: 0.4;
		user-select: none;
	}
}
