:root {
	body {
		font-family: "Manrope", sans-serif;
	}

	// THEME MODE
	&[theme="dark"] {
		$theme-name: theme-dark;
		@import "dark";
		// Use
		@import "style";
		color: $text-color;
	}

	&[theme="light"] {
		$theme-name: theme-light;
		@import "light";
		// Use
		@import "style";
		color: $text-color;
	}
}
