.footer-container {
	.footer-content {
		display: flex;

		.copyright {
			text-align: center;
		}

		.social-icon-group {
			text-align: right;

			img {
				margin: 0 10px;
				cursor: pointer;
				&:hover {
					opacity: 0.5;
				}
			}
		}
	}

	@media (max-width: 425px) {
		.footer-content {
			display: block;
			text-align: center;

			.copyright {
				margin: 10px 0;
			}

			.social-icon-group {
				text-align: center;
			}
		}
	}
}
