// Public
.header-public {
	background: $block-bg;
	border-bottom: 1px solid $border-1;
	text-align: center;
	height: 72px;
	z-index: 100;

	img {
		height: 100%;
		padding: 15px 0;
	}
}
//
.header {
	.adminButton {
		svg {
			path {
				fill: $color-1;
			}
		}
	}

	@media (max-width: 400px) {
		.select-channel {
			width: 130px;
		}
	}

	@media (min-width: 1200px) and (max-width: 1350px) {
		.logo {
			height: 39px;
		}

		.tab {
			margin: 10px 11px;
			font-size: 17px;
		}

		.buttonOther {
			margin-left: 10px;

			.adminButton + div {
				margin-left: 13px;
			}
		}
	}
}
// Notification
.notification {
	.popover {
		background: $block-bg;

		font-size: 16px;
		color: $text-color;

		max-width: 527px;
		border-color: $border-1;
		box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.1);

		.no-data {
			padding: 21px 28px;
		}

		&-header {
			background: $block-bg;
			border-color: $border-1;
			padding: 21px 28px;
			font-size: 18px;
			font-weight: 700;
		}
		&-body {
			max-height: calc(100vh - 130px);
			overflow: scroll;
			color: $text-color;
			padding: 0;
			margin-bottom: -16px;
			border-bottom-left-radius: 0.3rem;
			border-bottom-right-radius: 0.3rem;

			.noti-list {
				padding: 0;

				.item {
					padding: 22px 28px;

					&-icon {
						margin-right: 15px;
						color: #ffffff;
						background-color: #096af5;
					}

					&-text {
						margin: 0;

						p {
							font-size: 14px;
							color: $text-color-2;
						}
					}

					&-time {
						margin-left: 15px;
						white-space: nowrap;

						font-size: 14px;
						color: $text-color-2;
					}
				}
			}
		}
	}

	// Bottom
	.bs-popover-bottom .popover-header::before,
	.bs-popover-auto[x-placement^='bottom'] .popover-header::before {
		border-color: $block-bg;
	}

	.bs-popover-bottom > .arrow::after,
	.bs-popover-auto[x-placement^='bottom'] > .arrow::after {
		border-bottom-color: $block-bg;
	}

	.bs-popover-bottom > .arrow::before,
	.bs-popover-auto[x-placement^='bottom'] > .arrow::before {
		border-bottom-color: $border-1;
	}

	// Top
	.bs-popover-top .popover-header::before,
	.bs-popover-auto[x-placement^='top'] .popover-header::before {
		border-color: $block-bg;
	}

	.bs-popover-top > .arrow::after,
	.bs-popover-auto[x-placement^='top'] > .arrow::after {
		border-top-color: $block-bg;
	}

	.bs-popover-top > .arrow::before,
	.bs-popover-auto[x-placement^='top'] > .arrow::before {
		border-top-color: $border-1;
	}
}
