@use "sass:math";

@function switchColor($color-light, $color-dark) {
   @if $theme-name == theme-light {
      @return $color-light;
   } @else {
      @return $color-dark;
   }
}
.dashboard-view-container {
   .search-box-holder {
      display: flex;
      justify-content: flex-end;
      height: 55px;
      margin-bottom: 39px;
      .filter-box {
         width: 143px;
         height: 55px;
         margin-right: 25px;
         background: $block-bg;
         border-radius: 8px;
         color: $text-color;
         .MuiOutlinedInput-notchedOutline {
            border: none;
            // width: 143px;
         }
         .MuiSelect-select:focus {
            background: $block-bg;
            border-radius: 8px;
         }
         .MuiOutlinedInput-root {
            width: 143px;
         }
      }
      .search-box {
         width: 100%;
         max-width: 623px;
         height: 55px;
         border-radius: 8px;
         background: $block-bg;
         position: relative;

         input {
            width: 100%;
            // max-width: 593px;
            height: 55px;
            border-radius: 8px;
            border: none;
            padding: 0 15px;
            background: $block-bg;
            color: switchColor(rgba(17, 17, 17, 0.38), rgba(255, 255, 255, 0.6));
            font-size: 18px;
            &:focus {
               outline: none;
            }
         }
         .search-btn {
            position: absolute;
            right: 0;
            width: 55px;
            height: 55px;
            border-radius: 8px;
            line-height: 55px;
            text-align: center;
            background: switchColor(#096AF5, #253B58);
            cursor: pointer;
         }
      }
   }
   .data-sythetic-block {
      display: flex;
      justify-content: space-between;
      margin: 0 0 20px 0;
      flex-wrap: wrap;
      .data-sythetic-card {
         float: left;
         width: 23.5%;
         color: rgba(255, 255, 255, 0.9);
         height: 100%;
         display: block;
         font-size: 16px;
         text-align: center;
         min-height: 190px;
         border-radius: 10px;
         display: flex;
         flex-wrap: wrap;
         align-items: center;
         justify-content: center;
         padding: 27px 0;

         @media only screen and (max-width: 700px) {
            width: 49%;
            margin-bottom: 8px;
            min-height: 160px;
            padding: 11px 0;
         }
         div {
            width: 100%;
            text-align: center;
         }
         .data-sythetic-path {
            &.icon {
               font-size: 38px;
            }
            &.count {
               font-size: 36px;
            }
         }
      }
      .data-sythetic-card:nth-child(1) {
         background: #096af5;
      }
      .data-sythetic-card:nth-child(2) {
         background: #37c882;
      }
      .data-sythetic-card:nth-child(3) {
         background: #ffa73f;
      }
      .data-sythetic-card:nth-child(4) {
         background: #ff6b6b;
      }
   }

   .trans-chart-block {
      padding: 20px 0;
      background: $block-bg;
      border-radius: 10px;
      margin-bottom: 30px;
      .chart-top-row {
         display: flex;
         justify-content: space-between;
         align-items: center;
         flex-wrap: wrap;
         margin-bottom: 15px;

         .top-chart-left {
            font-size: 18px;
            font-weight: 700;
            color: $text-color;
            display: flex;
            // padding: 10px 0;
            div {
               // padding: 0 17px;

               & + div {
                  margin-left: 40px;
               }
               cursor: pointer;
            }
            .switch-chart {
               margin-top: -7px;
            }
         }
         .top-chart-right {
            height: 40px;
            background: $small-bg;
            border-radius: 8px;
            min-width: 300px;
            // margin-right: 15px;
            display: flex;
            align-items: center;
            padding: 2px;

            .chart-props {
               height: 100%;
               border-radius: 8px;
               cursor: pointer;
               color: #6b6b6d;
               min-width: 40px;
               line-height: 37px;
               padding: 0 15px;
               text-align: center;
               @media (max-width: 450px) {
                  padding: 0 5px;
               }
               :hover {
                  background: #fff;
               }
               &.chart-props-active {
                  background: #fff;
               }
            }
            @media (max-width: 701px) {
               margin-top: 15px;
            }
            @media (max-width: 450px) {
               min-width: 0;
            }
         }
      }
      .highcharts-container {
         // padding: 0 27px;
      }
   }
   .section {
      background: $block-bg;
      border: 1px solid $border-1;
      border-radius: 10px;
      margin-bottom: 30px;
      width: 100%;

      &-title {
         padding: 33px 26px;
         font-size: 18px;
         font-weight: 700;
      }
      &-break {
         margin: 0 26px;
         border-bottom: 1px solid $border-1;
      }
      &-content {
         // padding: 0 61px;
      }
   }

   .trans-table-holder {
      margin-bottom: 40px;
      background: $block-bg;
      // border: 1.32481px solid #e5e9f2;
      border-radius: 10.5985px;
      .trans-header {
         font-size: 18px;
         font-weight: bold;
         border-bottom: solid 1px $border-1;
         display: flex;

         > div {
            margin: 25px 27px;
            cursor: pointer;
            border-bottom: none;

            &:hover {
               color: #096af5;
            }

            + div {
               margin-left: 10px;
            }
         }
         .active {
            color: #096af5;
         }
      }
      //background: rgba(229, 233, 242, 0.1);
      .filter-row {
         display: flex;
         justify-content: space-between;
         color: switchColor(rgba(17, 17, 17, 0.6), #a7a9a9);
         font-size: 16px;
         height: 55px;
         line-height: 55px;
         padding: 0 27px;
         align-items: center;
         .filter-icon {
            width: 24px;
            height: 24px;
            margin-top: -5px;
            margin-right: 7px;
         }
         .trans-table-filter-option {
            width: 38px;
            height: 38px;
            background: switchColor(rgba(229, 233, 242, 0.4), #373a3d);
            border-radius: 4px;
            text-align: center;
            line-height: 38px;
            cursor: pointer;
         }
      }
      .trans-table-header {
         border-bottom: solid 1px $border-1;
         width: 100%;
         .table-th {
            // &:nth-child(1) {
            //    padding-left: 50px;
            // }
            font-family: "Manrope", sans-serif;
            font-size: 18px;
            font-weight: bold;
            line-height: 45px;
            height: 45px;
            background: switchColor(#f8f8fa, #2d2f31);
            .th-icon {
               width: 20px;
               height: 20px;
               color: #aeb4c1;
               margin-bottom: 5px;
            }
            .funnel-icon-holder {
               width: 38px;
               height: 38px;
               text-align: center;
               line-height: 38px;
               display: inline-block;
               border-radius: 4px;
               margin-left: 15px;
               background: switchColor(rgba(229, 233, 242, 0.4), #404144);
               .funnel-icon {
                  width: 13px;
                  height: 12px;
               }
            }
         }
      }
      table {
         border-collapse: collapse !important;
      }
      .MuiTableContainer-root {
         &div {
            overflow: scroll;
            max-width: 100vw;
            ::-webkit-scrollbar {
               width: 10px;
            }

            /* Track */
            ::-webkit-scrollbar-track {
               background: #f1f1f1;
            }

            /* Handle */
            ::-webkit-scrollbar-thumb {
               background: #888;
            }

            /* Handle on hover */
            ::-webkit-scrollbar-thumb:hover {
               background: #555;
            }
         }
      }
      .trans-table {
         border-collapse: collapse; 
         // background: $block-bg;
         .trans-table-row {  
            &:hover {
               cursor: pointer;
               background: switchColor(#f8f8fa, #2d2f31);
            }
            // td {
            //    display: inline-block;
            //    border-bottom: solid 1px #e6e7e9;
            // }
            .trans-table-cell {
               font-size: 16px;
               font-family: "Manrope", sans-serif;
               border-bottom: solid 1px switchColor(#e6e7e9, rgba(255, 255, 255, 0.001));
               .text-in-blue {
                  color: #096af5;
                  line-height: 24px;
               }
               .tx-hash {
                  display: inline-block;
                  max-width: 324px;
                  white-space: nowrap;
                  overflow: hidden;
                  text-overflow: ellipsis;
               }
               .btn-copy {
                  display: inline-block;
                  margin: -10px 0 0 15px;
               }
               .eye-icon {
                  display: inline-block;
                  width: 24px;
                  height: 24px;
                  border-radius: 4px;
                  text-align: center;
                  line-height: 24px;
                  margin-right: 10px;
                  margin-top: -5px;
                  background: switchColor(#f5f6fa, #373a3d);
               }
               &.txnFee-cell {
                  color: switchColor(rgba(17, 17, 17, 0.6), rgba(255, 255, 255, 0.6));
               }
            }
            .blue-bg-box {
               background: switchColor(rgba(9, 106, 245, 0.2), rgba(9, 106, 245, 0.2));
               height: 38px;
               line-height: 38px;
               padding: 0 11px;
               display: inline-block;
               width: auto;
               border-radius: 4px;
               font-size: 14px;
            }
         }
      }
   }

   @media (max-width: 768px) {
      .search-box-holder {
         margin-bottom: 25px;

         .filter-box {
            margin-right: 10px;
         }
      }
   }
}
