@import 'footer';
@import 'main';
// Page
@import 'landing-page';
@import 'dashboard-view';
@import 'timeline-block';
@import 'chart-stats';
@import 'block-detail';
@import 'chart-org';

// Chrome - Autocomplete: Background for dark mode
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
	-webkit-box-shadow: 0 0 0 30px $block-bg inset !important;
}

.switch {
	position: relative;
	display: inline-block;
	width: 90px;
	height: 34px;
	top: 8px
  }
  
  .switch input {display:none;}
  
  .slider {
	position: absolute;
	cursor: pointer;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: #cccccc;
	-webkit-transition: .4s;
	transition: .4s;
  }
  
  .slider:before {
	position: absolute;
	content: "";
	height: 20px;
	width: 18px;
	left: 4px;
	bottom: 8px;
	background-color: white;
	-webkit-transition: .4s;
	transition: .4s;
  }
  
  input:checked + .slider {
	background-color: #3d95ff;
  }
  
  input:focus + .slider {
	box-shadow: 0 0 1px #2196F3;
  }
  
  input:checked + .slider:before {
	-webkit-transform: translateX(50px);
	-ms-transform: translateX(60px);
	transform: translateX(60px);
  }
  
  /*------ ADDED CSS ---------*/
  .on
  {
	display: none;
  }
  
  .on
  {
	color: white;
	position: absolute;
	transform: translate(-50%,-50%);
	top: 50%;
	left: 35%;
	font-size: 12px;
	font-weight: 600;
	font-family: Verdana, sans-serif;
  }

  .off
  {
	color: white;
	position: absolute;
	transform: translate(-50%,-50%);
	top: 50%;
	left: 60%;
	font-size: 12px;
	font-weight: 600;
	font-family: Verdana, sans-serif;
  }
  
  input:checked+ .slider .on
  {display: block;}
  
  input:checked + .slider .off
  {display: none;}
  
  /*--------- END --------*/
  
  /* Rounded sliders */
  .slider.round {
	border-radius: 34px;
  }
  
  .slider.round:before {
	border-radius: 50%;}