.chart-stats-container {
    width: auto;
    background: $block-bg;
    padding: 0 27px;

    .chart-tab-group {
        &.nav-tabs {
            border-bottom: 1px solid switchColor(#E5E9F2, rgba(229, 233, 242, 0.1)) !important;
        }
        .chart-tab {
            color: $text-color !important;
            background-color: $block-bg;
            cursor: pointer;
            border-bottom: 1px solid switchColor(#E5E9F2, rgba(229, 233, 242, 0.1));
            &.active {
                color: #096AF5 !important;
                border-top: 1px solid switchColor(#E5E9F2, rgba(229, 233, 242, 0.1)) !important;
                border-left: 1px solid switchColor(#E5E9F2, rgba(229, 233, 242, 0.1)) !important;
                border-right: 1px solid switchColor(#E5E9F2, rgba(229, 233, 242, 0.1)) !important;
                border-bottom: 1px solid $block-bg !important;
            }
        }
    }
}