.pie {
	padding: 30px 0;

	&-chart {
		margin-top: 30px;
		width: 100%;
		height: 420px;

		& path.recharts-sector {
			stroke: $block-bg;
		}
	}

	&-legend {
		margin: 35px 30px 0;
		list-style: none;

		@media (min-width: 576px) {
			margin: 35px 61px 0;
		}

		&-row {
			margin-top: 10px;
			display: flex;
			align-items: center;

			&-icon {
				font-size: 40px;
				margin: -4px 5px 0 0;
			}

			&-title {
				font-size: 16px;
				font-weight: 500;
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;
				color: $text-color;
			}
		}
	}
}

@media (max-width: 425px) {
	.pie {
		&-chart {
			margin-top: 0;
			width: 95%;
			height: 300px;
			margin: auto;
		}

		&-legend {
			margin: 15px 10px 0;
		}
	}
}
