.detail-box {
	background: $block-bg;
	border-radius: 10px;
	padding: 30px;

	@media (max-width: 425px) {
		padding: 20px;
	}

	.box {
		&-title {
			font-size: 24px;
			font-weight: 700;
			margin-bottom: 25px;

			span {
				color: $text-color-2;
				font-size: 18px;
				font-weight: 500;
				margin-left: 14px;
			}

			&-sub {
				color: $color-2;
				font-size: 18px;
				font-weight: 700;
				margin-bottom: 22px;
			}
		}

		&-table {
			font-size: 18px;
			padding: 0 15px;

			.cell {
				border-top: 1px solid $border-1;
				padding: 25px 0;

				&:nth-child(2) {
					font-weight: bold;
					text-overflow: ellipsis;
					// white-space: pre-wrap;
					word-break: break-all;
					overflow: hidden;

					display: flex;
					align-items: center;

					.text {
						overflow: hidden;
						text-overflow: ellipsis;
						margin-right: 5px;
					}
				}
			}
		}
	}
}
